export default {
	data() {
		return {
			id: '',
			data: Object,
			baseUrl: "http://39.105.48.243:8080/crlink/"
		}
	},
	onLoad(e) {
		this.id = e.id;
		this.getNetDetail(this.id)
	},

	methods: {
		getNetDetail(e) {
			// user!ajaxGetCertDetail.action
			var that = this;
			var loginkey = uni.getStorageSync('loginKey');
			this.$api.post('user!ajaxGetCertDetail.action', {
				loginKey: loginkey,
				id: that.id
			}).then(res => {
				if (res.res.status == 0) {
					that.data = res.inf

				} else {
					uni.showToast({
						title: res.res.errMsg
					})
				}
			})
		},



		/****长按保存图片 */
		saveImage() {
			let that = this
			console.log("save");
			wx.getSetting({
				success(res) {
					//未授权 先授权 然后保存
					if (!res.authSetting['scope.writePhotosAlbum']) {
						wx.authorize({
							scope: 'scope.writePhotosAlbum',
							success(re) {
								that.saveToBlum();
							}
						})
					} else {
						//已授 直接调用保存到相册方法
						that.saveToBlum();
					}
				}
			})
		},

		//保存网络图片到相册方法
		saveToBlum: function() {
			wx.downloadFile({
				url: this.baseUrl + this.data.imgUrl,
				success: function(res) {
					wx.saveImageToPhotosAlbum({
						filePath: res.tempFilePath,
						success(result) {
							wx.showToast({
								title: '保存成功',
								icon: 'success'
							})
						}
					})
				}
			})
		},



	}


}
